.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0s linear 0.3s;
  z-index: 100;
}

.modal.js-show {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.25);
  overflow-y: auto;
}

.modal__overlay::after {
  content: "";
  width: 0;
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.modal__body {
  position: relative;
  display: inline-block;
  padding: 20px;
  max-width: 690px;
  width: 90%;
  margin-bottom: 100px;
  margin-top: 100px;
  padding-bottom: 200px;
  vertical-align: middle;
  background-color: #fff;
}

@media (max-width: 767px) {
