  background-image: url("../img/climat-online2.jpg");
}

.program-card-mini--my-comfort .program-card-mini__overlay {
  background-image: url("../img/my-comfort2.jpg");
}

.program-card-mini--mobile-control .program-card-mini__overlay {
  background-image: url("../img/mobile-control2.jpg");
}

@media (max-width: 1279px) {
  .program-card {
    max-width: 226px;
    min-height: 284px;
  }
  .program-card__btn-block a:first-child, .program-card__btn-block a:nth-child(2) {
    display: none;
  }
  .program-card__btn-block a:nth-child(3) {
    padding: 0;
    display: block;
  }
  .program-card__overlay {
    background-position: center;
  }
  .program-card__overlay p {
    display: none;
  }
  .program-card:hover .program-card__overlay,
  .program-card-mini:hover .program-card-mini__overlay {
    top: 70px;
  }
  .program-card--climat-online .program-card__overlay {
    background-image: url("../img/climat-online2.jpg");
  }
  .program-card--my-comfort .program-card__overlay {
    background-image: url("../img/my-comfort2.jpg");
  }
  .program-card--mobile-control .program-card__overlay {
    background-image: url("../img/mobile-control2.jpg");
  }
  .program-card-mini {
    max-width: 226px;
  }
}

@media (max-width: 767px) {
  .program-card,
  .program-card-mini {
    font-weight: 300;
    font-size: 11px;
    line-height: 16px;
    max-width: 170px;
    min-height: 230px;
  }
  .program-card__body,
  .program-card-mini__body {
    padding: 15px;
  }
  .program-card__header,
  .program-card-mini__header {
    margin-bottom: 20px;
  }
  .program-card__overlay,
  .program-card-mini__overlay {
    top: 60px;
    height: calc(100% - 60px);
  }
  .program-card:hover .program-card__overlay,
  .program-card-mini:hover .program-card-mini__overlay {
    top: 60px;
  }
}
